import React, { Component } from 'react';
import './MCon.css';

class MResultDis extends Component {
    constructor(props){
        super(props);
        
        this.saveToProfile = this.saveToProfile.bind(this);
    }
    
    saveToProfile(){
        
    }
    
    render() {
      var soft = [];
      var hard = [];
      var missarr = [];
      var fontstyle = {
          color:"#292929"
      }

      for(var i in this.props.resultsoft){
          var comp = (
                    <tr key={i}>
                        <td>{i}</td>
                        <td>{this.props.resultsoft[i].restimes}</td>
                        <td>{this.props.resultsoft[i].jobtimes}</td>
                    </tr>
          )
        soft.push(comp);
      };

      for(var i in this.props.resultshard){
          var comp = (
                    <tr key={i}>
                        <td>{i}</td>
                        <td>{this.props.resultshard[i].restimes}</td>
                        <td>{this.props.resultshard[i].jobtimes}</td>
                    </tr>
          )
        hard.push(comp);
      }

      for(var i=0; i<this.props.missingskill.length; i++){
          var comp = (
              <tr key={i}>
                        <td>{this.props.missingskill[i].crse_numb}</td>
                        <td>{this.props.missingskill[i].crse_name}</td>
                        <td>{this.props.missingskill[i].crse_url}</td>
                    </tr>
          )
          missarr.push(comp);
      }

    return (
        <div>
            <section className="rsection">
             <br/><br/><br/>
            <div className="container">
                <div id="percentage">
                    <div className="progress" id="progress"></div>
                </div>
                <br/><br/>
                <div className="resultcontainer">
                  <div className="resultbox">
                      <p className="rtitle">Comparison</p>
                        <table id="mTable">
                            <caption>Soft Skills</caption>
                            <thead>
                               <tr>
                                <th style={fontstyle}>Keywords</th>
                                <th style={fontstyle}>Resume</th>
                                <th style={fontstyle}>Job Description</th>
                                </tr> 
                            </thead>
                            <tbody>
                                {soft}
                            </tbody>
                        </table>

                        <table>
                            <caption>Hard Skills</caption>
                            <thead>
                               <tr>
                                <th style={fontstyle}>Keywords</th>
                                <th style={fontstyle}>Resume</th>
                                <th style={fontstyle}>Job Description</th>
                                </tr> 
                            </thead>
                            <tbody>
                                {hard}
                            </tbody>
                        </table>
                  </div>

                  <div className="resultbox">
                      <p className="rtitle">Suggestion</p>
                  </div>


                  <div className="resultbox">
                      <p className="rtitle">Course Recommandation</p>
                      <div id="courseRec">
                        <table>
                            <caption>Hard Skills</caption>
                            <thead>
                               <tr>
                                <th>Course Number</th>
                                <th>Course Name</th>
                                <th>Course URl</th>

                                </tr> 
                            </thead>
                            <tbody>
                                {missarr}
                            </tbody>
                        </table>
                    </div>
                    <button>Save</button>
                  </div>

              </div>
            </div>
        </section>

      </div>
    );
    }
}

export default MResultDis;


