import React, { Component } from 'react';
import './App.css';
import MControl from './comp/MControl';
import MResultDis from './comp/MResultDis';
import MTry from './comp/MTry';
import MTry3 from './comp/MTry3';

class App extends Component {
    constructor(props){
        super(props);
        
        this.state ={
            matching:{}
            
        }
        
        this.pushMatching = this.pushMatching.bind(this);
        
    }
    pushMatching(data){
        var temp = this.state.matching;
        temp.push(data);
        
        this.setState({
            matching:temp
        });
        
        console.log(temp);
    }

    render() {
        var comp = <MControl 
            pushMatching={this.pushMatching}
            />;
        return (
          <div className="App">
            <MControl 
            pushMatching={this.pushMatching}
            />
          </div>
        );
    }
}

export default App;



